import { Typography, RateBlock } from '@app/shared/components';

import clsx from 'clsx';
import SpikeletIcon from '@app/shared/svg-icons/icons/spikelets/SpikeletIcon';
import styles from './AchievementsSection.module.css';

export const AchievementsSection = () => {
  return (
    <div className="flex mt-12 self-center items-start h-fit">
      <SpikeletIcon className=" w-auto" />
      <div className="flex flex-col mt-3 sm:mt-2 md:mt-0">
        <Typography
          bold="medium"
          font="montserrat"
          color="primary"
          center
          className={clsx(styles['comment-count'])}>
          150K+
        </Typography>
        <Typography
          bold="medium"
          className={styles['average-rating']}
          font="montserrat"
          center>
          4.8 out of 5 stars
        </Typography>
        <RateBlock
          maxRate={5}
          currentRate={5}
          className="h-full w-auto gap-2.5 mt-2.5 self-center"
          starsStyle="sm:w-4 sm:h-4 md:w-[18px] md:h-[18px]"
        />
      </div>
      <SpikeletIcon className=" w-auto transform scale-x-[-1]" />
    </div>
  );
};
